<template>
<svg :x="x" :y="y" :width="w" :height="h" @mousedown="mousedown" class="cursor-pointer" @mouseenter="mouseenter" @mouseleave="mouseleave" @mouseup="mouseup">
    <foreignObject :width="w" :height="h" x="0" y="0">
        <div v-if="node.type === NodeTypes.text" :class="node.class">{{ node.content }}</div>
        <div v-if="node.type === NodeTypes.image" :class="node.class">
            <img :width="w" :src="node.content" />
        </div>
    </foreignObject>
</svg>
</template>

<script>
import {
    ref
} from "vue";
import NodeTypes from "@/values/NodeTypes.js";

export default {
    props: {
        onMouseDown: Function,
        node: Object
    },
    setup(props, {
        emit
    }) {
        const x = ref(props.node.x);
        const y = ref(props.node.y);
        let isMoving = false;
        const moveMe = function (mx, my) {
            isMoving = true;
            x.value += mx;
            y.value += my;
        };
        const mousedown = function (e) {
            e.stopPropagation();
            e.preventDefault();
            if (props.onMouseDown) {
                props.onMouseDown(moveMe);
            }
        };

        const w = ref(0);
        const h = ref(0);
        if (props.node.type === NodeTypes.text) {
            w.value = 400;
            h.value = 80;
        } else if (props.node.type === NodeTypes.image) {
            w.value = 150;
            h.value = 150;
        }

        const mouseenter = function () {
            //console.log("enter");
        };
        const mouseleave = function () {
            //console.log("mouseleave");
        };
        const mouseup = function () {
            if (isMoving) {
                isMoving = false;
                return;
            }
            emit("onNodeClicked");
            isMoving = false;
        };

        return {
            NodeTypes,
            x,
            y,
            mousedown,
            w,
            h,
            mouseenter,
            mouseleave,
            mouseup
        };
    }
}
</script>
