<template>
<v-row>
    <v-col cols="12" sm="5">
        <v-sheet class="ml-8 my-4" rounded="lg" height="90vh" color="#e0e9f5" @dragover="onDragover">
            <div class="flex flex-col items-center justify-end h-full p-4">
                <div class="w-full h-5/6 overflow-scroll flex flex-col-reverse">
                    <div class="flex items-start py-2 mb-8" :class="{ 'flex-row-reverse': mes.self }" v-for="mes in history" :key="mes.nodeId">
                        <v-avatar v-if="mes.self" class="border" image="images/test_me.png"></v-avatar>
                        <v-avatar v-else class="border" image="images/test_ai.png"></v-avatar>
                        <div class="mx-4 p-4 bg-white rounded-lg">
                            <div class="cursor-move" :class="{ 'text-right': mes.self }">
                                <div v-if="mes.type === NodeTypes.text">
                                    <span v-html="mes.content[0]" draggable="true" @dragstart="startDrag(mes.content[0], mes.type)"></span>
                                </div>
                                <div v-if="mes.type === NodeTypes.image" class="flex gap-4">
                                    <v-img v-for="(url, index) in mes.content" :key="index" :width="150" aspect-ratio="4/3" cover :src="url" draggable="true" @dragstart="startDrag(url, mes.type)"></v-img>
                                </div>
                                <div v-if="mes.type === NodeTypes.buttons">
                                    <my-buttons :content="mes.content" @button-clicked="pickOption" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="w-full flex flex-col items-center">
                    <v-text-field class="w-full" v-model="chatToSend" label="Chat to AI" clearable @keyup.enter="test2()"></v-text-field>
                    <v-btn class="bg-zinc-100 my-2" block :disabled="sending" :loading="sending" @click="test2()">
                        Send
                    </v-btn>
                </div>
            </div>
        </v-sheet>
    </v-col>
    <v-col cols="12" sm="7">
        <v-sheet class="mr-8 my-4" height="90vh" rounded="lg" color="#e0e9f5" @dragover="onDragover" @drop="onDrop">
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="w-full h-full canvas" @mousemove="mosemove" @mouseup="mouseup">
                <my-node v-for="(node, index) in textNodes" :key="index" :node="node" :on-mouse-down="onNodeMouseDown" @on-node-clicked="clickCanvasNode(node)" />
            </svg>
        </v-sheet>
    </v-col>
</v-row>
</template>

<script>
import {
    v4 as uuidv4
} from "uuid";
import {
    ref,
    onMounted
} from "vue";
import {
    Configuration,
    OpenAIApi
} from "openai";
import {
    openModal
} from "jenesius-vue-modal";
import NodeTypes from "@/values/NodeTypes.js";
import Prompts from "@/values/Prompts.js";
import MyNode from "@/components/MyNode.vue";
import MyButtons from "@/components/MyButtons.vue";
import Course from "@/values/lesson.json";
import Services from "@/logics/services.js";
import MyFunctions from "@/logics/functions.js";
import MyDialog from "@/components/MyDialog.vue";
// import Process from "@/logics/lessonProcessor.js";

export default {
    name: "HomeView",
    components: {
        MyNode,
        MyButtons
    },
    setup() {
        const userId = uuidv4();
        console.log(userId);
        const chatToSend = ref("");
        const history = ref([]);
        const sending = ref(true);
        const configuration = new Configuration({
            apiKey: NodeTypes.k2 + NodeTypes.k1 + NodeTypes.k3,
        });
        const openai = new OpenAIApi(configuration);

        const store = new Map();
        const courseData = new Map();
        Course.sections.forEach(function (section) {
            if (section.id) {
                courseData.set(section.id, section);
            }
        });

        let actions = [];
        const proceedCourse = function (id) {
            if (!courseData.has(id)) {
                alert("Course data is not complete!");
                return;
            }
            if (history.value[0]) {
                if (history.value[0].type === NodeTypes.buttons) {
                    history.value.shift();
                }
            }
            sending.value = true; //Always lock input textfield

            setTimeout(async function () {
                const section = courseData.get(id);

                if (section.info) {
                    let info = section.info;
                    if (section.varNames) {
                        info = MyFunctions.assignVar(info, section.varNames, store);
                    }
                    history.value.unshift({
                        content: [info],
                        self: false,
                        type: NodeTypes.text,
                        nodeId: uuidv4()
                    });
                }
                console.log("section: " + section.id);
                actions = JSON.parse(JSON.stringify(section.actions))
                await performActions();
            }, 1000);
        };
        const performActions = async function () {
            while (actions.length) {
                const act = actions.shift();
                console.log(act.type);
                if (act.type === "next") {
                    proceedCourse(act.next);
                    break;
                } else if (act.type === "saveInput") {
                    if (!store.has("input")) {
                        sending.value = false;
                        actions.unshift(act); //Put the action back for processing user input
                        break; //breakable action
                    }
                    store.set(act.varName, store.get("input"));
                    store.delete("input");
                } else if (act.type === "rephrase") {
                    const inputText = MyFunctions.assignVar(act.input, act.varNames, store);
                    console.log("rephrase:" + inputText);
                    const reply = await Services.serviceRephrase(openai, inputText, 3, 10);
                    store.set(act.save, reply);
                    const confirm = MyFunctions.assignVar(act.text, act.varNames, store);
                    history.value.unshift({
                        content: [confirm],
                        self: false,
                        type: NodeTypes.text,
                        nodeId: uuidv4()
                    });
                    const node = {
                        content: [],
                        self: true,
                        type: NodeTypes.buttons,
                        nodeId: uuidv4()
                    }
                    act.options.forEach(function (option) {
                        node.content.push({
                            text: option.text,
                            on: false,
                            next: option.next,
                            reply: option.reply
                        });
                    });
                    history.value.unshift(node);
                } else if (act.type === "options") {
                    const node = {
                        content: [],
                        self: true,
                        type: NodeTypes.buttons,
                        nodeId: uuidv4()
                    };
                    act.options.forEach(function (option) {
                        node.content.push({
                            text: option.text,
                            on: false,
                            next: option.next,
                            reply: option.reply,
                            save: option.save
                        });
                    });
                    history.value.unshift(node);
                } else if (act.type === "publish") {
                    let publishText = MyFunctions.assignVar(act.text, act.varNames, store);
                    textNodes.value.push({
                        content: publishText,
                        x: 100,
                        y: textNodes.value.length * 100 + 100,
                        type: NodeTypes.text,
                        class: "text-blue-500 text-lg"
                    });
                } else if (act.type === "writeStoryIntroduction") {
                    act.seed = MyFunctions.assignVar(act.seed, act.varNames, store);
                    const reply = await Services.serviceWriteStoryIntroduction(openai, act.seed, 3, 50);
                    store.set("writeStoryIntroduction", reply);
                    history.value.unshift({
                        content: [reply],
                        self: false,
                        type: NodeTypes.text,
                        nodeId: uuidv4()
                    });
                } else if (act.type === "listIdeasFromIdea") {
                    const idea = MyFunctions.assignVar(act.idea, act.varNames, store);
                    const reply = await Services.serviceListIdeasFromIdea(openai, idea, act.term, act.desc, act.number);
                    const items = Object.values(JSON.parse(reply))[0];
                    const node = {
                        content: [],
                        self: true,
                        type: NodeTypes.buttons,
                        nodeId: uuidv4()
                    };
                    items.forEach(function (item) {
                        node.content.push({
                            text: item,
                            on: false,
                            next: "",
                            reply: "",
                            save: act.save,
                            append: act.append
                        });
                    });
                    history.value.unshift(node);
                    break; //breakable action
                } else if (act.type === "listStoryIdeas") {
                    const seed = MyFunctions.assignVar(act.seed, act.varNames, store);
                    const reply = await Services.serviceListStoryIdeas(openai, seed, act.number, act.grade, act.maxWords);
                    const ideas = Object.values(JSON.parse(reply))[0];
                    const node = {
                        content: [],
                        self: true,
                        type: NodeTypes.buttons,
                        nodeId: uuidv4()
                    };
                    ideas.forEach(function (idea) {
                        node.content.push({
                            text: idea,
                            on: false,
                            next: "",
                            reply: "",
                            save: act.save
                        });
                    });
                    if (act.options) {
                        act.options.forEach(function (option) {
                            node.content.push({
                                text: option.text,
                                on: false,
                                next: option.next,
                                reply: option.reply,
                                save: option.save
                            });
                        });
                    }
                    history.value.unshift(node);
                    break; //breakable action
                } else if (act.type === "makeImage") {
                    const text = MyFunctions.assignVar(act.text, act.varNames, store);
                    const images = await Services.serviceMakeImage(openai, text);

                    const node = {
                        content: images,
                        self: false,
                        type: NodeTypes.image
                    };
                    history.value.unshift(node);
                }
            }
        };
        onMounted(function () {
            proceedCourse("001");
        });

        const textNodes = ref([]);
        // textNodes.value.push({
        //     content: "Drag EVERYTHING to this canvas!",
        //     x: 300,
        //     y: 100,
        //     type: NodeTypes.text,
        //     class: "text-blue-500 text-lg"
        // });

        const pickOption = async function (button) {
            if (button.done) {
                return;
            }
            button.on = true;
            button.done = true;
            history.value.shift();
            history.value.unshift({
                content: [button.text],
                self: true,
                type: NodeTypes.text
            });

            if (button.reply) {
                history.value.unshift({
                    content: [button.reply],
                    self: false,
                    type: NodeTypes.text
                });
            }
            if (textNodes.value.length && button.cancel) {
                textNodes.value.pop(); //Remove last published canvas text
            }
            if (button.save) {
                store.set(button.save, button.text);
            }
            if (button.append) {
                const value = store.get(button.append);
                if (value) {
                    store.set(button.append, value + ", " + button.text);
                } else {
                    store.set(button.append, button.text);
                }
            }
            if (button.next) {
                proceedCourse(button.next);
            } else {
                await performActions();
            }
        };

        const test2 = async function () {
            if (!chatToSend.value) {
                return;
            }
            const input = chatToSend.value;
            chatToSend.value = "";
            if (sending.value) {
                alert("We are not anticipating input text now...");
                return;
            }
            store.set("input", input);
            if (history.value[0]) {
                if (history.value[0].type === NodeTypes.buttons) {
                    history.value.shift();
                }
            }
            history.value.unshift({
                content: [input],
                self: true,
                type: NodeTypes.text
            });
            sending.value = true;
            await performActions();
        };

        const test = async function () {
            if (sending.value) {
                return;
            }
            if (!chatToSend.value) {
                return;
            }
            sending.value = true;

            const input = chatToSend.value;
            chatToSend.value = "";
            const check = await checkIntention(input);
            history.value.unshift({
                content: [input],
                self: true,
                type: NodeTypes.text
            });
            if (check === 0) {
                await getChat(input);
            } else if (check === 1) {
                await getImages(input);
            }

            sending.value = false;
        };

        const getChat = async function (input) {
            const response = await openai.createChatCompletion({
                model: "gpt-3.5-turbo",
                messages: [{
                    role: "user",
                    content: input
                }],
                user: userId
            });

            console.log(response.data);
            history.value.unshift({
                content: [response.data.choices[0].message.content],
                self: false,
                type: NodeTypes.text
            });
        };

        const getImages = async function (input) {
            const response = await openai.createImage({
                prompt: input,
                n: 2,
                size: "512x512",
                user: userId
            });

            const text = {
                content: [],
                self: false,
                type: NodeTypes.image
            };
            console.log(response.data);
            response.data.data.forEach(function (image) {
                text.content.push(image.url);
            });
            history.value.unshift(text);
        };

        const checkIntention = async function (input) {
            const prompt = Prompts.checkIntention.text.replace(Prompts.checkIntention.input, input);

            const response = await openai.createCompletion({
                model: "text-davinci-003",
                prompt,
                temperature: 0,
            });
            console.log(response);

            const check = response.data.choices[0].text.trim().toLowerCase();
            console.log(check);
            if (check.includes(Prompts.checkIntention.expectations[0])) {
                return 0;
            } else if (check.includes(Prompts.checkIntention.expectations[1])) {
                return 1;
            }
            alert("Unknown error");
            return -1;
        };

        let draggingNode = null;

        const startDrag = function (content, type) {
            draggingNode = {
                content,
                class: "",
                type
            }
        };

        const onDragover = function (e) {
            e.preventDefault();
        };

        const onDrop = function (e) {
            e.preventDefault();
            if (draggingNode) {
                const rect = e.target.getBoundingClientRect();
                draggingNode.x = e.clientX - rect.left;
                draggingNode.y = e.clientY - rect.top;
                textNodes.value.push(draggingNode);
                draggingNode = null;
            }
        };

        let nodeCallbackMove = null;
        const onNodeMouseDown = function (callback) {
            nodeCallbackMove = callback;
        };
        let mx = 0;
        let my = 0;
        const mosemove = function (e) {
            if (nodeCallbackMove) {
                e.preventDefault();
                nodeCallbackMove(e.x - mx, e.y - my);
            }
            mx = e.x;
            my = e.y;
        };
        const mouseup = function () {
            nodeCallbackMove = null;
        };

        const clickCanvasNode = function (node) {
            const props = {
                content: node.content
            };
            openModal(MyDialog, props);
        };

        return {
            NodeTypes,
            chatToSend,
            history,
            sending,
            test,
            startDrag,
            onDragover,
            onDrop,
            textNodes,
            onNodeMouseDown,
            mosemove,
            mouseup,
            pickOption,
            test2,
            clickCanvasNode
        };
    }
}
</script>

<style scoped>
.canvas {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    border-style: solid;
    border-color: #fff;
    border-radius: 12px;
}
</style>
